export default {
    name: "share",
  data(){
      return{
        type: 'pc',
        api: this.GLOBAL.baseURL,
        isRegist:false,
        isTel:true,
        countryNumber:[
          {value:1,label:'+86(中国)'}
        ],
        PC_code:'+86(中国)',
        PC_tel:'',
        PC_SMSCode:'',
        PC_SMS:'发送验证码',
        PC_telTime:60,
        PC_email:'',
        PC_emailCode:'',
        PC_emailSMS:'发送验证码',
        PC_emailWord:'',
        PC_emailReWord:'',
        PC_emailTime:60,
        emailForbid:false,
        telForbid:true,
        changeTime:'',
        userID:'',
        userName:''
      }
  },
  methods:{
      getToken(){
        this.$http.post(this.api + '/home/Index/token')
          .then(res=>{
            if(res.data.code == 1){
              localStorage.setItem('token',res.data.data)
              this.getUser()
            }
          })
      },
      getUser(){
        this.$http.post(this.api + '/home/User/get_otheruser',{
          token: localStorage.getItem('token'),
          user_id: this.userID
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              this.userName = data.family_name + data.middle_name + data.name?data.family_name + data.middle_name + data.name:'匿名用户'
            }else if(res.data.code == 3){
              this.getUser()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
    toLogin(){
      var baseurl = 'https://www.allptp.cn'
      window.open(this.api + "/QqLogin"+'?baseurl='+encodeURIComponent(baseurl)+'&token='+ localStorage.getItem('token')+'&userId='+this.top_id,'_self')
    },
    toWeixin(){
      var baseurl = 'https://www.allptp.cn'
      window.open( this.api + '/WechaLogin?token='+ localStorage.getItem('token')+'&baseurl='+encodeURIComponent(baseurl)+'&userId='+sessionStorage.getItem('Share'),'_self')
    },
    toHome(){
      this.$router.push('/')
    },
    tohome(){
      this.$router.push('/index')
    },
    toTelPhone(){
      this.isRegist = !this.isRegist
      this.isTel = true
    },
    toEmail(){
      this.isRegist = !this.isRegist
      this.isTel = false
    },
    otherRegist(){
      this.isRegist = false
      this.PC_telTime=60
      this.PC_emailTime = 60
      this.PC_SMS = '发送验证码'
      this.PC_emailSMS = '发送验证码'
      this.PC_tel = ''
      this.PC_SMSCode = ''
      this.PC_email = ''
      this.PC_emailCode = ''
      this.PC_emailWord = ''
      this.PC_emailReWord = ''
      clearInterval(this.changeTime)
    },
    emailRegist(){
      if(!this.PC_email){
        this.$message({
          type: 'info',
          message: '请填写邮箱'
        })
      }else if(!this.PC_emailCode){
        this.$message({
          type: 'info',
          message: '请填写邮箱验证码'
        })
      }else if(!this.PC_emailWord){
        this.$message({
          type: 'info',
          message: '请填写密码'
        })
      }else if(!this.PC_emailReWord){
        this.$message({
          type: 'info',
          message: '请重复填写密码'
        })
      }else{
        this.$http.post(this.api + '/RegisterEmail',{
          token: localStorage.getItem('token'),
          email: this.PC_email,
          sms_code: this.PC_emailCode,
          password: this.PC_emailWord,
          repassword: this.PC_emailReWord,
          top_user_id: sessionStorage.getItem('Share')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type:'success',
                message:'注册成功！'
              })
              localStorage.setItem('isLogin',true)
              this.$router.push('/')
              this.PC_email= ''
              this.PC_emailCode=''
              this.PC_emailSMS='发送验证码'
              this.PC_emailWord=''
              this.PC_emailReWord=''
            }else if(res.data.code == 3){
              this.emailRegist()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    sendEmail(){
      var regex = /^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/;
      if(!regex.test(this.PC_email)){
        this.$message({
          type: 'error',
          message: '邮箱格式错误'
        })
      }else{
        this.$http.post(this.api + '/CheckEmail',{
          token: localStorage.getItem('token'),
          email: this.PC_email
        })
          .then(res=>{
            if(res.data.code == 1){
              this.checkEmail()
            }else if(res.data.code == 0){
              this.PC_email = ''
              this.$message({
                type: 'info',
                message: '改邮箱已经注册'
              })
            }
          })
      }
    },
    checkEmail(){
      this.$http.post(this.api + '/email',{
        token: localStorage.getItem('token'),
        toemail: this.PC_email,
        flag: 1
      })
        .then(res=>{
          if(res.data.code == 1){
            this.emailForbid = true
            this.changeTime = setInterval(()=>{
              this.PC_emailTime--
              this.PC_emailSMS = '重新发送（'+this.PC_emailTime+ '）'
              if(this.PC_emailTime <= 0){
                this.PC_emailTime = 60
                this.emailForbid = false
                this.PC_emailSMS = '重新发送'
                clearInterval(this.changeTime)
              }
            },1000)
          }else if(res.data.code == 3){
            this.login()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    telRegist(){
      if(!this.PC_tel){
        this.$message({type: 'info',message:'填写手机号'})
      }else if(!this.PC_SMSCode){
        this.$message({type: 'info',message:'填写验证码'})
      }else if(!this.PC_code){
        this.$message({type: 'info',message:'选择手机区号'})
      }else{
        this.$http.post(this.api + '/home/User/login_sms',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.PC_code),
          mobile: this.PC_tel,
          sms_code: this.PC_SMSCode,
          top_user_id: sessionStorage.getItem('Share')
        })
          .then(res=>{
            if(res.data.code == 1){
              localStorage.setItem('isLogin',true)
              this.$message({
                type:'success',
                message:'注册成功！'
              })
              this.$router.push('/')
            }else if(res.data.code == 3){
              this.telRegist()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    sendTel(){
      var myreg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if(!myreg.test(this.PC_tel)){
        this.$message({type:'info',message:'手机号格式有问题'})
      }else{
        this.$http.post(this.api + '/home/User/send_msg',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.PC_code),
          mobile: this.PC_tel,
          flag:2
        })
          .then(res=>{
            if(res.data.code== 1){
              this.telForbid = true
              this.changeTime = setInterval(()=>{
                this.PC_telTime--
                this.PC_SMS = '重新发送（'+this.PC_telTime+ '）'
                if(this.PC_telTime <= 0){
                  this.PC_telTime = 60
                  this.telForbid = false
                  this.PC_SMS = '重新发送'
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.sendTel()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    checkTel(){
      var myreg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if(this.PC_tel){
        if(!myreg.test(this.PC_tel)){
          this.$message({type:'info',message:'手机号格式有问题'})
        }else{
          this.$http.post(this.api + '/home/User/validate_mobile',{
            token: localStorage.getItem('token'),
            mobile: this.PC_tel,
            m_code: parseInt(this.PC_code)
          })
            .then(res=>{
              if(res.data.code == 1){
                this.telForbid = false
                this.sendTel()
              }else if(res.data.code == 0){
                this.PC_tel = ''
                this.$message({
                  type: 'info',
                  message: '该手机号已经注册'
                })
              }
            })
        }
      }

    }

  },
  mounted(){
    this.getToken()
  },
  created(){
    let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
    if(flag){
      this.type = 'wap'
    }else{
      this.type = 'pc'
    }
    if(this.$route.query.userId){
      this.userID = this.$route.query.userId
      sessionStorage.setItem('Share',this.$route.query.userId)
    }
  },
}



